@use 'styles/colors';
@import 'styles/base';

:host {
  width: 100%;
  display: flex;
  flex-direction: column;
  max-width: 350px;
  gap: 15px;

  .item {
    display: flex;
    align-items: center;
    padding: 25px;
    cursor: pointer;
    background: #FFFFFF;
    box-shadow: 0 0 20px 5px rgba(0, 0, 0, 0.03);

    span {
      @include proxima(16px, 400, colors.$foreground_dark);
      opacity: 0.65;
    }

    .icon {
      font-size: 20px;
      margin-right: 15px;
      opacity: 0.3;
      transform: translateY(-2px);
    }

    &:hover {
      background: #FCFCFC;
    }

    &.active {
      span {
        color: colors.$accent;
      }

      .icon {
        background-color: colors.$accent;
      }
    }
  }

  @media screen and (max-width: 900px) {
    max-width: unset;
  }
}
